body {
  background-color: #242526;
  margin: 0;
  padding: 0;
  font-family: cairo, sans-serif;
  direction: ltr;
}

#root {
  width: 70%;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  box-shadow: 2px 2px 10px #3f4850;
  border-radius: 20px;
}

/* width */

::-webkit-scrollbar {
  width: 10px;
}

/* Track */

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */

::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.mb-30 {
  margin-bottom: 30px;
}

.fa-quote-left,
.fa-quote-right {
  font-size: 35px;
  color: rgb(179, 0, 0);
}
.quote {
  text-align: center;
  font-size: 40px;
  font-weight: bold;
  color: antiquewhite;
}
.author {
  margin: 10px;
  text-align: right;
  font-size: 25px;
  font-style: italic;
  font-weight: 800;
  font-family: cursive;
  color: #fff;
}
i{
  color: rgb(179, 0, 0);
}
@media (min-width: 1200px) {
  .container{
    margin-top: 100px !important;
    margin-bottom: 100px !important;
  }
}
@media (max-width: 1200px), (max-height: 600px) {
  body {
    padding: 75px 0;
  }
  .quote {
    font-size: 30px;
  }
  #root {
    width: 90%;
    position: static;
    margin: 0 auto;
    transform: translate(0 , 0);
  }
}